@charset "UTF-8";
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

*[class*=title],
*[class*=heading],
*[class*=text] {
  margin: 0;
}

*[class*=link] {
  color: inherit;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.block-center {
  width: calc(100% - 40px);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .block-center {
    width: calc(100% - 60px);
    max-width: 708px;
  }
}
@media (min-width: 1190px) {
  .block-center {
    width: calc(100% - 200px);
    max-width: 1220px;
  }
}

.page--column {
  height: 100%;
}

.page__body {
  margin: 0;
  min-width: 280px;
}
.page__body--column {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.page__header {
  height: auto;
}

.page__content--column {
  flex-grow: 1;
}

.page__footer {
  height: auto;
}

.page-header {
  width: auto;
}

/* Стили для Главная страница */
.main-page {
  background-color: #ffffff;
}

/* Стили для Страница каталога */
.catalog-page {
  background-color: #ffffff;
}

/* Стили для Страница формы заказов */
.order-page {
  background-color: #ffffff;
}